.transformation{

  &__container{
    position: relative;
    padding-bottom: 50px;
  }

  &__title{
    margin: 0;
    max-width: 806px;
    font-family: Merriweather;
    font-size: 54px;
    font-weight: 400;
    line-height: 65px;
    letter-spacing: 0px;
    text-align: left;
    color: #313131;

  }
  &__subtitle{
    font-family: GolosText;
    display: inline-block;
    max-width: 280px !important;
    color: #3057A2;
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0px;
    text-align: left;
    margin-bottom: 50px;
  }
  &__list{
    margin: 0 auto;
    max-width: 1222px;
    display: grid;
    grid-template-columns: 394px 394px 394px;
    grid-template-rows: 112px 112px 136px;
    gap: 20px;
  }
  &__item{
    position: relative;
    display: flex;
    gap: 20px;
    padding: 20px;
    background-image: url('../img/bg-mobile.jpg');
    width: 394px;
    height: 112px;

    &:nth-child(3){
      grid-row: 2 span;
      height: 244px;
    }
    &:nth-child(6){
     height: 136px;
    }
    &:nth-child(7){
      width: 803px;
      height: 136px;
      grid-column: span 2;
    }
  }
  &__img-hidden{
    position: absolute;
    right: 50px;
    bottom: 10px;
    z-index: 10;
  }
  &__img{
    position: absolute;
    left: 20px ;
    top: 150px;
    z-index: 100;
  }
  &__digital{
    width: 36px;
    height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 100%;
    font-family: Golos Text;
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0px;
    text-align: left;
    color: #313131;
  }


}
.swiper-content{
  display: none;
}

.swiper{
  margin: 0;
  width: 335px;
  height: 380px;
}
.swiper-inner{
  position: relative;
  width: 335px;
  height: 300px;
  background-image: url('../img/bg-mobile.jpg');
  background-size: cover;
}
.swiper-text{
  margin: 0;
  max-width: 302px;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;

  &-long{
    max-width: 446px;
  }
}
.swiper-digital{
  position: absolute;
  top: 50px;
  left: 20px;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 100%;
  font-family: Golos Text;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;
  color: #313131;

  &-bottom{
    top: 170px;
  }
  &-center{
    top: 130px;
  }
}
.swiper-button-next{
	position: absolute;
  top: 365px;
	right: 70px;
  border: none;
  width: 36px;
  height: 36px;
  background-image: url('../img/next.png');
  background-repeat: no-repeat;
  background-position: center;

&::after {
  display: none;
  }
}
.swiper-button-prev{
	position: absolute;
	top: 365px;
	left: 70px;
  object-fit: contain;
  width: 36px;
  height: 36px;
  background-image: url('../img/prev.png');
  background-repeat: no-repeat;
  background-position: center;

  &::after{
    display: none;
  }
}

.swiper-pagination-bullet{
  border-radius: 100%;
  width: 10px;
  height: 10px;
  background-color: #313131;
}

@media (max-width: (1440px)){
  .transformation__img-hidden{
    right: 20px;
    bottom: 60px;
    opacity: 0.8;
  }
}
@media (max-width: (1250px)){
  .transformation__list{
    max-width: 800px;
   grid-template-columns: repeat(2, 1fr);
  }
  .transformation__item{
    width: 420px;
    padding: 20px 10px 10px 10px;
    &:nth-child(7){
      width: 420px;
      height: 136px;
      grid-column: span 1;
    }
  }
  .transformation__img-hidden{
    right: 0px;
    top:  -20px;
    opacity: 0.8;
  }
  .swiper-text{
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;

    &-long{
      max-width: 300px;
    }
  }
}
@include tablet {
  .transformation__hidden{
    display: none;
  }
  .swiper-content{
    display: block;
    margin: 0 auto;
    max-width: 340px;
    position: relative;
  }
  .transformation__img{
    position: absolute;
    left: 10px;
    top: -150px;
    z-index: 100;
  }
  .swiper-text{
    padding-top: 50px;
    padding-left: 70px;
    max-width: 209px;
    margin: 0;

    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: left;
  }
  .swiper-text-two{
    padding-top: 30px;
    padding-left: 70px;
    max-width: 209px;
    margin: 0;
    font-size: 18px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: left;
  }
  .transformation__title{
    margin: 0 auto;
    max-width: 335px;
    font-size: 36px;
    font-weight: 400;
    line-height: 40px;
    letter-spacing: 0px;
    text-align: left;
  }
  .transformation__subtitle{
    margin: 0;
    max-width: 335px;
    font-family: Golos Text;
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: left;
    color: #3057A2;
    margin-bottom: 150px;
  }
}
