.hero{
  &__container{
    position: relative;
    padding: 130px 30px 0px 30px;
    overflow: hidden;
    background-image: url('../img/mainbgBig.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: right;
    min-height: 760px;
    // padding: 0;
  }
  &__title{
    margin: 0;
    font-family: Merriweather;
    font-size: 60px;
    font-weight: 400;
    line-height: 66px;
    letter-spacing: 0px;
    text-align: left;
    text-transform: uppercase;
    max-width: 542px;
    color: #313131;
  }
  &__subtitle{
    margin: 0;
    max-width: 542px;
    text-transform: uppercase;
    font-family: Merriweather;
    font-size: 60px;
    font-weight: 400;
    line-height: 66px;
    letter-spacing: 0px;
    text-align: center;
    color: #313131;
    margin-bottom: 30px;
  }
  &__descr{
    margin: 0;
    max-width: 542px;
    font-size: 18px;
    font-weight: 400;
    line-height: 23px;
    letter-spacing: 0px;
    text-align: center;
    margin-bottom: 40px;
  }
  &__btns{
    display: flex;
    gap: 30px;
  }
  &__link{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #1f1f1f;
    width: 263px;
    height: 72px;
    border-radius: 66px;
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
    letter-spacing: 0px;
    text-align: center;
    color: #fff;
    padding: 0px 48px;

    &-white{
      border: 1px solid #313131;
      background-color: #fff;
      color: #1f1f1f;
    }

    &:not(:last-child){
      margin-bottom: 20px;
    }
  }

}

@include small-tablet{
  .hero__title{
    margin: 0 auto;
    max-width: 360px;
    font-size: 36px;
    font-weight: 400;
    line-height: 42px;
    letter-spacing: 2.6px;
    text-align: left;
  }
  .hero__subtitle{
    margin: 0 auto;
    max-width: 365px;
    font-size: 36px;
    font-weight: 400;
    line-height: 42px;
    letter-spacing: 2.6px;
    text-align: center;
    margin-bottom: 35px;
    padding-left: 20px;
    padding-top: 6px;
  }
  .hero__descr{
    margin: 0 auto;
    max-width: 335px;
    font-size: 18px;
    font-weight: 700;
    line-height: 23px;
    letter-spacing: 0px;
    text-align: left;
    color: #1f1f1f;
    margin-bottom: 35px;
  }
}
@include mobile{
  .hero__container{
    padding: 130px 0px 0px 0px;
    height: 912px;
    background-image: url('../img/main-bg.jpg');
}
  .hero__btns{
    display: block;
    max-width: 340px;
    margin: 0 auto;
  }
  .hero__link{
    padding: 10px;
    width: 335px;
    height:47px;
    color: #fff;
    border-radius: 66px;
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
    letter-spacing: 0px;
    text-align: center;

    &-white{
      border: none;
      background-color: #fff;
      color: #1f1f1f;
    }
  }
  .hero__descr{
    font-weight: 400;
  }
}
