.tournament{
  padding-bottom: 100px;

  &__container{
    position: relative;
    overflow: hidden;
  }
  .ticker {
    position: absolute;
    overflow: hidden;
    border-top: 1px solid var(--dark-color);
    border-bottom: 1px solid var(--dark-color);
    margin-top: auto;
    top: 0;
    left: 0;
  }

  &__top{
    margin: 0 auto;
    max-width: 1222px;
    padding-top: 150px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin-bottom: 50px;
  }
  &__bottom{
    max-width: 704px;
  }
  &__right{
    margin: 0 auto;
    display: flex;
    gap: 50px;
    max-width: 1222px;
  }

  &__title{
    margin: 0 ;
    text-transform: uppercase;
    font-family: Merriweather;
    font-size: 36px;
    font-weight: 400;
    line-height: 49px;
    letter-spacing: 0px;
    text-align: left;
  }
  &__subtitle{
    margin: 0;
    text-transform: uppercase;
    font-family: Merriweather;
    font-size: 36px;
    font-weight: 400;
    line-height: 49px;
    letter-spacing: 0px;
    text-align: left;

    span{
    font-weight: 700;
      color: #F54932;
    }

  }
  &__hidden{
    display: none;
    text-transform: uppercase;
    font-size: 28px;
    font-weight: 400;
    line-height: 38px;
    letter-spacing: 0px;
    text-align: left;
    margin-bottom: 20px;
    span{
      display: block;
      font-weight: 700;
        color: #F54932;
      }
  }
  &__img{
    width: 394px;
    height: 250px;
  }
  &__img-bottom{
    width: 474px;
    height: 527px;
  }
  &__text{
    margin: 0;
    padding: 5px 0px;
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0px;
    text-align: left;
    opacity: 0.7;
    color: #313131;
  }
  &__descr{
    margin: 0;
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0px;
    text-align: left;
    padding-bottom: 20px;
  }
  &__item{
    max-width: 335px;
    &:not(:last-child){
      border-bottom: 2px solid #d0d0d0;
    }
  }
  &__text-bottom{
    margin: 0;
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0px;
    text-align: left;
    color: #3057A2;
    }
  &__price-old{
    position: relative;
    &::before{
      content: '';
      position: absolute;
      top: 12px;
      left: 0px;
      width: 70px;
      height: 2px;
      background-color: #F54932;
    }
  }
}

@include tablet {
  .tournament__title{
    max-width: 305px;
    font-size: 28px;
    font-weight: 400;
    line-height: 38px;
    letter-spacing: 0px;
    text-align: left;
    text-transform: uppercase;
    margin-bottom: 20px;
  }
  .tournament__top{
    flex-direction: column;
    align-items: center;
  }
  .tournament__subtitle{
    display: none;
  }
  .tournament__hidden{
    display: block;
  }
  .tournament__img{
    width: 335px;
    height: 212px;
    margin-bottom: 30px;
  }
  .tournament__right{
   flex-direction: column;
   align-items: center;
  }
  .tournament__img-bottom{
    width: 335px;
    height: 402px;
    margin-bottom: 20px;
  }
  .tournament__text-bottom{
    max-width: 335px;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0px;
  }
}
