.participants{

  &__container{
    padding: 150px 0px 0px 0px;
    position: relative;
  }
  &__title{
    margin: 0;
    position: absolute;
    top: 50px;
    left: 100px;
    font-family: Merriweather;
    font-size: 54px;
    font-weight: 400;
    line-height: 65px;
    letter-spacing: 0px;
    text-align: left;
    text-transform: uppercase;
    color: #313131;
    padding: 100px 0px 30px 15px;
  }
}
.slider{
  margin: 0 auto;
  padding-top: 100px;
  position: relative;
  overflow: hidden;
  width: 1222px;
  height: 600px;
  margin-bottom: 100px;

}
.slider-inner{
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 394px;
  height: 462px;
}
.button-next{
	position: absolute;
  top: 30px;
  right: 0;
  border: none;
  width: 36px;
  height: 36px;
  background-image: url('../img/next.png');
  background-repeat: no-repeat;
  background-position: center;
  z-index: 100;

&::after {
  display: none;
  }
}
.button-prev{
	position: absolute;
  top: 30px;
  left:  1100px;
  object-fit: contain;
  width: 36px;
  height: 36px;
  background-image: url('../img/prev.png');
  background-repeat: no-repeat;
  background-position: center;

  &::after{
    display: none;
  }
}

.pagination{
  position: absolute;
  top: 15px;
  left: 550px;
}
.slider-img-box{
  background-image: url('../img/alex.svg');
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 100%;
  width: 320px;
  height: 320px;
  margin-bottom: 20px;
}
.slider-name{
  margin: 0;
  color: #313131;
  font-size: 24px;
  font-weight: 600;
  line-height: 29px;
  letter-spacing: 0px;
  text-align: center;
  margin-bottom: 10px;
}
.slider-status{
  margin: 0;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: left;
  color: #313131;
  opacity: 0.6;
  margin-bottom: 20px;
}
.slider-link{
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 62px;
  border: 1px solid #3057A2;
  width: 113px;
  height: 35px;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0px;
  color:#3057A2 ;
  cursor: pointer;
  z-index: 100;
}
.swiper-button-disabled{
  opacity: 0.6;
}
@media (max-width: (1200px)){

  .slider{
    max-width: 1000px;
  }
  .slider-img-box{
    width: 244px;
    height: 244px;
    margin-bottom: 20px;
  }
  .button-prev{
    position: absolute;
    top: 30px;
    left:  800px;
  }
  .pagination{
    position: absolute;
    top: 15px;
    left: 370px;
  }
  .button-next{
    position: absolute;
    top: 30px;
    right: 60px;
  }
}

@include tablet{
  .slider{
    max-width: 350px;
    padding-top: 30px;
  }
  .participants__title{
  font-family: Merriweather;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  letter-spacing: 0px;
  text-align: left;
  }
  .button-prev{
    position: absolute;
    top: 540px;
    left:  270px;
  }
  .button-next{
    position: absolute;
    top: 540px;
    right: 270px;
  }
  .pagination{
    position: absolute;
    top: 525px;
    left: 0px;
  }
}
@include small-tablet{
  .participants__container{
    padding: 0px;
  }
  .participants__title{
    position: static;
    margin: 0;
    // position: absolute;
    // top: -50px;
    // left: 0px;
  }
  .slider{
    width: 335px;
    height: 580px;
    margin-bottom: 70px;
  }
  .button-prev{
    position: absolute;
    top: 540px;
    left:  100px;
  }
  .button-next{
    position: absolute;
    top: 540px;
    right: 100px;
  }
  .pagination{
    position: absolute;
    top: 525px;
    left: 0px;
  }
}
